*{
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding: 60px;
}

input{
  width: 100%;
  border-radius: 5px;
  border: 1px solid #ccc;
  color: grey;
  padding: 7px;
}

.catName{
  padding: 10px;
  background-color: rgb(224, 224, 224);
  border-radius: 5px;
  
}

.wrapcontent{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  
}

.card{
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 15px;
  margin-right: 15px; 
  display: flex; 
  flex-direction: column;
}

.card p{
  padding: 5px;
  align-self: center;
}

.card img{
  max-height: 170px;
}